@import url(https://fonts.googleapis.com/css?family=Anton|Gugi|Lato|Roboto|Sofia&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Import fonts from the Google fonts: https://fonts.google.com/ */

:root{
    --light-gray: #2b3032a1;
    --primary-color: #45ccb8;
    --border: #2b303218;
    --text-color: #ff686b;
    --roboto: "Roboto", sans-serif;
    --gugi: "Gugi", cursive;
    --sofia: "Sofia", cursive;
}

.primary-color{
    color: #45ccb8;
    color: var(--primary-color);
}

.bg-primary-color{
    background-color: #45ccb8 !important;
    background-color: var(--primary-color) !important;
}

.text-color{
    color: #ff686b;
    color: var(--text-color);
}

.roboto{
    font-family: "Roboto", sans-serif;
    font-family: var(--roboto);
}

.lightblue{
    color: lightblue;
}

/* Header */
header{
    background-color: #45ccb8;
    background-color: var(--primary-color);
    font-family: "Roboto", sans-serif;
    font-family: var(--roboto);
}

header .site-title{
    font-family: "Gugi", cursive;
    font-family: var(--gugi);
}

.site-title a{
    text-decoration: none;
    color: white;
}

.container .dropdown-toggle,
.container .dropdown-item{
    font-size: 0.7em;
}

.header-links a{
    font-size: 0.9em;
    text-decoration: none;
    color: white;
}

/* Navigation Menu */
.navbar-nav{
    padding: 0.8em 0em;
}

.navbar-nav .nav-link{
    font-size: 0.9em;
    color: #2b3032a1 !important;
    color: var(--light-gray) !important;
}

.navbar-nav .nav-item .nav-link{
    padding: 0 1.3em;
}

.navbar-nav .basket-icon{
    margin-right: 6.5em !important;
}

/* Slider-One */
.site-slider{
    position: relative;
}

.position-top{
    position: absolute;
    top: 45%;
}

.site-slider .slider-btn .prev,
.site-slider .slider-btn .next{
    background-color: #45ccb8;
    background-color: var(--primary-color);
    padding: 1rem 1.5rem;
    border-radius: 10rem;
    color: white;
    margin: 0 1rem;
    opacity: 0;
    -webkit-transition: opacity 1s ease;
    transition: opacity 1s ease;
}

.site-slider:hover .slider-btn .prev,
.site-slider:hover .slider-btn .next{
    opacity: 1;
}

.left-0{
    left: 0;
}

.right-0{
    right: 0;
}

/* Slider-One - Slick Dots */
.slick-dots{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 1rem 0;
    list-style-type: none;
    position: absolute;
    bottom: 0;
    right: 50%;
}

.slick-dots li{
    margin: 0 0.6rem;
}

.slick-dots button{
    display: block;
    width: 0.5rem;
    height: 0.5rem;
    padding: 0;
    border: none;
    background-color: white;
    text-indent: -99999px;
    border-radius: 100%;
}

.slick-dots li.slick-active button{
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.205);
}

/* Slider-Two */
.site-slider-two{
    position: relative;
    height: 30%;
}

.site-slider-two .product{
    height: 25rem;
    position: relative;
    overflow: hidden;
}

.site-slider-two .product img{
    width: 90%;
    margin: auto;
}

.site-btn.btn-span{
    padding: 0.8rem 0.9rem;
    border-radius: 4rem;
    border: 1px solid #2b303218;
    border: 1px solid var(--border);
    background-color: white;
    color: #2b3032a1;
    color: var(--light-gray);
}

.slider-two .slick-track .product:hover span{
    background-color: #45ccb8;
    background-color: var(--primary-color);
}

/* Horizontal Line */
.hr{
    border: 1px dotted #2b303218;
    border: 1px dotted var(--border);
}

/* Feature Section */
.container .features{
    padding: 3rem 0;
}

.container .features h1{
    font-family: car(--gugi);
}

.container .features p{
    padding: 0.5% 25%;
    font-size: 0.9rem;
}

/* Slider-Three */
.site-slider-three{
    position: relative;
    margin-bottom: 5rem;
}

.site-slider-three .product{
    height: 100%;
    padding: 0em;
}

.site-slider-three .cart-details{
    margin-top: 0.5rem;
    padding: 0.2rem;
}

.rating i{
    font-size: 0.7rem;
    color: lightblue;
}

.site-slider-three .pro-price span{
    color: #ff686b;
    color: var(--text-color);
}

.product .cart .site-btn:hover{
    background-color: #45ccb8;
    background-color: var(--primary-color);
    color: white;
}

/* Slider-Four */
.site-slider-four{
    position: relative;
    margin-top: 3rem; 
}

.slider-four{
    padding: 0 0.4rem;
}

.slider-four .product img{
    width: 80%;
    border: 1px solid #2b303218;
    border: 1px solid var(--border);
}

.newSeller{
    margin-top: 8rem;
}

.slider-brand{
    position: relative;
    margin: 4rem 0;
}

.slider-brand .slider-five img{
    width: 90%;
}

/* Out Client */
.slider-client{
    width: 100%;
    height: 100%;
    background: url(/estore/static/media/clientSays.c1440ef4.jpg);
    background-size: cover;
    padding: 8% 0%;
    overflow: hidden;
}

.slider-box{
    width: 85%;
    height: 100%;
    background: rgba(255, 255, 255, 0.350);
    margin: auto;
    border-radius: 0.4rem;
}

.slider-six p{
    padding: 2% 15%;
    line-height: 32px;
    font-family: "Roboto", sans-serif;
    font-family: var(--roboto);
    color: #2b3032a1;
    color: var(--light-gray);
}

.slider-six .slick-dots{
    bottom: -4rem;
    left: 50%;
}

.slider-six .slick-dots button{
    background-color: #2b303218;
    background-color: var(--border);
}

.slider-six .slick-dots li.slick-active{
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    background-color: #45ccb8;
    background-color: var(--primary-color);
    box-shadow: 1px 1px 2px 1px #2b303218;
    box-shadow: 1px 1px 2px 1px var(--border);
}

.site-info{
    margin-top: 10rem;
}

/* Footer */
footer h4{
    padding: 4rem 1rem 0;
    font-family: "Roboto", sans-serif;
    font-family: var(--roboto);
}
footer .social i{
    padding: 3% 1%;
}

footer .col-md-2 small{
    color: #2b3032a1;
    color: var(--light-gray);
    padding: 0.7em 0;
}

footer .follow-us div{
    width: 33%;
    height: 8rem;
    background: lightblue;
}
